body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stop-watch {
  box-sizing: border-box;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stop-watch__details {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.stop-watch__second-hand {
  z-index: 2;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite hand-move;
  position: absolute;
  bottom: 50%;
}

.stop-watch__minute-hand {
  z-index: 1;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite hand-move;
  position: absolute;
  bottom: 50%;
}

.stop-watch__circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stop-watch--speed-up .stop-watch__second-hand {
  animation: 10s linear infinite hand-move;
}

.stop-watch--speed-up .stop-watch__minute-hand {
  animation: 600s steps(60, end) infinite hand-move;
}

@keyframes hand-move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
